
/**
* 样式清0
*/
* { -webkit-box-sizing: border-box; box-sizing: border-box;margin: 0;padding: 0;color: #333;font-family: "Microsoft Yahei","Helvetica Neue",Helvetica,Arial,"Hiragino Sans GB","Heiti SC","WenQuanYi Micro Hei",sans-serif;
}
html, body { width: 100%; height: 100%;
}
html { font-size: 16px;
}
img { border: none;
}
ul, ol, li { list-style: none;
}
input, select, textarea { outline: none; border: none;
}
textarea { resize: none;
}
a, a:focus, a:hover { text-decoration: none;
}
i { font-style: normal;
}
strong { font-weight: normal;
}
/*input, textarea {-webkit-appearance: none;-moz-appearance: none;}*/
/**
* 清浮动
*/
.clear_f:after { content: ""; display: block; clear: both; height: 0;
}
.clear_f { *zoom: 1;
}
/**
* 左右浮动
*/
.float_l { float: left;
}
.float_r { float: right;
}
/**
* table
*/
table { border-collapse:collapse;
}
th, td { font-weight:normal; padding:10px; border: 1px solid deepskyblue;
}
body {
  max-width: 800px;
  margin: 0 auto;
  background-color: #efefef;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Helvetica, Arial, sans-serif, "Apple Color Emoji", "Segoe UI Emoji", "Segoe UI Symbol";
}

div.container-fluid {
  padding: 0;
}
div.card {
  position: static;
  display: block;
}
ul.nav>li.nav-item {
  margin: 0;
}
.nav-item a.nav-link {
  padding: 0.5rem 1rem;
}
div.card-header {
  padding: 0;
}
div.btm {
  height: calc(100vh - 115px);
}
div.card-body {
  padding: 0;
}
ul {
  padding: 0;
  margin: 0;
}
ul.card-header-pills {
  margin: 0;
}
.nav-pills a.nav-link {
  border-radius: 0;
}
.nav-item a {
  display: block;
}
.nav-item a.active,
.nav-item a.active:hover,
.nav-item a.active:focus {
  text-decoration: none;
}
.orderinfo {
  margin: auto;
  max-width: 800px;
  background-color: #fff;
  padding: 8px 5px;
  color: #666;
  font-size: 14px;
  font-weight: bold;
}
.orderinfo .info-arrow {
  background: url(data:image/png;base64,) no-repeat right;
  width: 38px;
}
.orderinfo>div {
  margin-right: 15px;
  line-height: 30px;
  color: #666;
  font-size: 14px;
}
.orderinfo>div.top {
  margin-right: 15px;
  line-height: 30px;
  color: #666;
  font-size: 14px;
}
.page-header {
  color: #fff;
  text-align: center;
  background: #159957;
  background-image: linear-gradient(120deg, #155799, #159957);
  padding: 3rem 4rem;
  margin-bottom: 30px;
}
.page-header h1 {
  margin: 0;
  font-size: 40px;
}
.page-header p {
  color: #ccc;
  margin: 0;
  margin-bottom: 30px;
}
.page-header a {
  display: inline-block;
  border: 1px solid #fff;
  margin-right: 10px;
  line-height: 40px;
  padding: 0 20px;
  border-radius: 4px;
  color: #fff;
  text-decoration: none;
  -webkit-transition: all 0.3s;
  transition: all 0.3s;
}
.page-header a:hover {
  background: #fff;
  color: #333;
}
.g-wraper {
  width: 1000px;
  margin: 0 auto;
  color: #666;
  font-size: 16px;
  line-height: 30px;
}
.m-part {
  margin-bottom: 0px;
}
.m-part::after {
  content: "";
  display: block;
  clear: both;
}
.m-part .title {
  font-size: 30px;
  line-height: 60px;
  margin-bottom: 10px;
  color: #333;
}
.m-part .mapbox {
  /*width: 550px;*/
  height: 420px;
  margin-bottom: 0px;
  margin-left: 0px;
  margin-top: 0px;
  /*float: left;*/
}
.m-part .info {
  margin: 0;
  padding: 0;
  list-style: none;
  line-height: 30px;
  margin-left: 620px;
}
.m-part .info span {
  display: block;
  color: #999;
}
.m-part ol {
  line-height: 40px;
  margin-left: 0;
  padding-left: 0;
}
.m-part pre {
  padding: 0px;
  line-height: 30px;
  border-radius: 3px;
  -webkit-box-shadow: 0 0 1px rgba(0, 0, 0, 0.5);
          box-shadow: 0 0 1px rgba(0, 0, 0, 0.5);
}
.m-footer {
  background: #eee;
  line-height: 60px;
  text-align: center;
  color: #999;
  font-size: 12px;
}
.m-footer a {
  margin: 0 5px;
  color: #999;
  text-decoration: none;
}
